.results {
    display: block;
    list-style-type: none;
    margin-bottom: 50px;
}

.results > li {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 15px 0;
}

.results > li > * {
    margin: 0 6px;
    width: 25%;
}

.results .noimage,
.results img {
    width: 12vw;
    max-width: 100%;
    height: 100%;
}

.results .notfound {
    font-style: italic;
    color: #999;
    height: 75px;
}

.results .customRating {
    -webkit-transform-origin: 50% 50%;
        -ms-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
    width: 5vw;
    margin: 0 15px;
} 

.ratings {
    text-align: left;
}


