.copyContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin-bottom: 50px;
}
.copyToClipboard {
    font-size: 11px;
    opacity: 0.6;
    -ms-flex-item-align: center;
        align-self: center;
    word-wrap:break-word;
    max-width: 40%;
}

.flex {
    display: -ms-flexbox;
    display: flex;
}

/* Animation */
@-webkit-keyframes fadeInOut {
    0% {opacity: 0}
    25% {opacity: 1}
    75% {opacity: 1}
    100% {opacity: 0}
}
@keyframes fadeInOut {
    0% {opacity: 0}
    25% {opacity: 1}
    75% {opacity: 1}
    100% {opacity: 0}
}

.buttonTitle.result {
    opacity: 0;
    display: block;
    color: #fff;
    font-size: 14px;
    -webkit-animation-name: fadeInOut;
            animation-name: fadeInOut;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
}


